@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Orbitron:wght@400;500;600;700;800;900&display=swap);
.header {
    background-image: url(/employee_directory/static/media/office.200a2880.png);
    height: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    position: relative;
    border-bottom: 10px #F90113 solid ;
}

.header__heading {
    font-size: calc(1.5rem + 1.5vw);
    color: #c1c1c1;
    font-family: 'Orbitron', sans-serif;
    font-weight: 800;
    position: absolute;
    left: 0; bottom: 0; right: 0;
    margin-bottom: 20px;
}

.header__img {
    height: calc(6.5rem + 2.0vw);;
    width: auto;
}

@media (max-width: 450px){
    .header {
        height: 36vmin;
    }

    .header__heading {
        display: none;
    }
}
