.table {
    box-shadow: 0px 2px 5px #909090;
    border-radius: 5px;
    white-space: nowrap;
}

.fa-sort {
    margin-left: 10px;
    text-shadow: 0 0 8px rgba(255, 0, 0, 0.6);
    visibility: hidden;
}

.table__heading:hover .fa-sort{
    cursor: pointer;
    visibility: visible;
}
.form-control:focus {
  border-color: #6d0000 !important;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(255, 0, 0, 0.6) !important;
}
